@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
    margin: 0;
    padding: 0;
}

#cv-container {
    margin: 1rem auto auto auto;
    width: 8.5in;
    height: 11in;
    border: 1px solid black;
    background-color: rgb(37, 37, 61);
}

.left-container {
    width: 50%;
}

.right-container {
    display: flex;
    flex-direction: column;
    width: 50%;
}

header {
    background-color: gray;
    
}
#personal-info {
  height: auto;
  display: flex;
  flex-direction: column;
  text-align: start;
  grid-gap: 0;
  gap: 0;
  padding: 2rem 2rem 1rem 2rem;
}

.name {
  border: none;
  background: none;
  outline: none;
  padding: 0;
  margin: 0;
  font-family: monospace;
  font-weight: 700;
  font-size: 2rem;
  color: white;
}

/* h2 {
  font-size: 1.1rem;
  margin: 0;
  font-weight: 500;
  color: rgb(173, 149, 13);
} */

.title {
  font-size: 1.1rem;
  margin: 0;
  padding: 0;
  font-weight: 500;
  color: rgb(173, 149, 13);
  border: none;
  background: none;
  outline: none;
  height: 1.1rem;
}

.description {
  font-size: 0.9rem;
  margin: 1rem 0 0 0;
  padding: 0;
  font-weight: 500;
  color: white;
  border: none;
  background: none;
  outline: none;
  height: auto;
  width: 100%;
  word-wrap: ;
}

.contact-div {
  width: 100%;
  background-color: rgb(18, 18, 29);
}

.contact-nav {
  height: 2rem;
  margin-bottom: 1rem;
  z-index: 100;
  width: 100%;
  color: white;
  display: flex;
  justify-content: center;
}

.contact-nav a {
  text-decoration: none;
  color: white;
}

/*========================== 
    navbar images
 ===========================*/

.link {
  font-size: 0.7rem;
  /* height: 100%; */
  display: flex;
  justify-content: start;
  align-items: center;
  margin-left: 1rem;
}

.img {
  width: 1rem;
  margin-right: 1rem;
}

.link text {
  margin-bottom: auto;
}

/*========================== 
    input styling
 ===========================*/

.link input {
  outline: none;
  border: none;
  background: none;
  color: white;
}

p,
button {
  margin-left: 1rem;
}

.experience-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  color: white;
  grid-gap: 0;
  gap: 0;
}

.work-experience-container {
  grid-column: 1;
  grid-row: 1 / 3;
  margin-left: 2rem;
  margin-top: 0;
}

.work-experience-component-container {
  margin-left: 1rem;
  margin-top: 0;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  align-items: start;
  align-content: start;
  grid-gap: 0;
  gap: 0;
}

h4 {
  font-size: 1rem;
  margin: 0.25rem;
}

h5 {
  font-size: 0.8rem;
  margin: 0.25rem;
}

h6 {
  font-size: 0.7rem;
  margin: 0.25rem;
}

p {
  font-size: 0.65rem;
  margin: 0.25rem;
}

.skills-container {
  grid-column: 2;
  margin-left: 2rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.education-container {
  grid-column: 2;
  margin-left: 2rem;
}

/* =========================================
 buttons
============================================*/

.add-img {
  background: none;
  background-color: none;
  border: none;
  width: 1.5rem;
}

.add-component-button {
  align-content: center;
  background: none;
  outline: none;
  border: none;
}

.work-experience-header,
h3,
.skill-header {
  display: flex;
  justify-content: start;
  align-content: center;
  margin: 0;
  margin-bottom: 0.5rem;
}

ul {
  margin: 0;
  margin-left: 1rem;
  padding: 0;
  font-size: 0.65rem;
}

li {
  margin: 0;
  padding: 0;
}

.remove-button {
  width: 1rem;
  margin: 0;
  padding: 0;
  position: absolute;
  -webkit-transform: translate(-150%, 10%);
          transform: translate(-150%, 10%);
}

.submit-cv {
  margin-left: 2rem;
}

/* =========================================
  lists
============================================*/

.input {
  outline: none;
  border: none;
  background: none;
  color: white;
  font-size: 0.65rem;
}

.list {
  font-size: 0.65rem;
}

/* ==========================================
  skills
============================================*/

.skill-tag {
  background-color: coral;
  display: flex;
  justify-content: center;
  border-radius: 5px;
  padding: 0.2rem 0.3rem 0.2rem 0.3rem;
  align-items: center;
  font-size: 0.75rem;
  grid-row: 1 / 2;
}

.skills-component-container {
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-bottom: 1rem;
  align-items: start;
}

.remove-skill-button {
  width: 1rem;
  margin: 0;
  padding: 0;
}

.skill-input {
  background: none;
  outline: none;
  border: none;
  width: 50px;
  color: white;
}

/* ==========================================
  ` education 
============================================*/

.education-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column: 2;
  justify-content: left;
  align-items: flex-start;
  align-content: flex-start;
  grid-gap: 0;
  gap: 0;
}

.education-header {
  display: flex;
}

.education-component-container {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}

.education-component {
  display: flex;
  flex-direction: column;
}

